<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <div class="not-allowed-wrap"  v-if="isShowNotAllowed"></div>
            <!---->
            <div class="common-btn-wrap">
                <el-button class="btn-cyan add-btn" @click="handleAdudit('multi')" :disabled="otherVariable.isSaving"></el-button>
                <el-button @click="handleRefresh"></el-button>
                <el-button @click="handleReset"></el-button>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @selectChange="selectChange"
                @getData="getSearchList"
            >
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @handleDetail="handleDetail"
                @handleSelectionChange="handleSelectionChange"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"
            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span v-if="!item.isCheck">{{ detailForm[item.prop] }}</span>
                                <span v-if="item.isCheck">{{ detailForm[item.prop] === 1 ? '':(detailForm[item.prop] === 0?'x':'') }}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap">
                    <div v-if="showButton" class="xs-audit-icon" @click="!otherVariable.isSaving && handleAdudit('alone')" :class="otherVariable.isSaving ? 'banClick' : ''"></div>
                    <div v-if="showUnAudit" class="xs-unaudit-icon" @click="!otherVariable.isSaving && handleUnAudit()" :class="otherVariable.isSaving ? 'banClick' : ''"></div>
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <div class="remark-wrap">
                        <div></div>
                        <div class="headspan scrollBar" :title="detailForm.remark">{{ detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <el-tabs v-model="activeName" class="common-content-tab order_style">
                        <el-tab-pane label="" name="first">
                            <el-table
                                v-if="isResize"
                                tooltip-effect="dark"
                                v-loading="loadingI"
                                :data="tabDataI"
                                @row-click="clickDetail"
                                border
                                :height="tableHeightI"
                                highlight-current-row
                            >
                                <el-table-column label="" align="left" width="120px">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.itemNo}}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" lign="left" width="120px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.matCode }}</span>
                                        </template>
                                </el-table-column>
                                <!--<el-table-column label="" align="center">-->
                                    <!--<el-table-column label="" lign="left" width="120px">-->
                                        <!--<template slot-scope="scope">-->
                                            <!--<span></span>-->
                                        <!--</template>-->
                                    <!--</el-table-column>-->
                                    <!--<el-table-column label="" lign="left" :show-overflow-tooltip="true">-->
                                        <!--<template slot-scope="scope">-->
                                            <!--<span></span>-->
                                        <!--</template>-->
                                    <!--</el-table-column>-->
                                <!--</el-table-column>-->
                                <!--<el-table-column label="" align="center">-->
                                    <!--<el-table-column label="" lign="left" width="120px">-->
                                        <!--<template slot-scope="scope">-->
                                            <!--<span></span>-->
                                        <!--</template>-->
                                    <!--</el-table-column>-->
                                    <!--<el-table-column label="" lign="left" :show-overflow-tooltip="true">-->
                                        <!--<template slot-scope="scope">-->
                                            <!--<span></span>-->
                                        <!--</template>-->
                                    <!--</el-table-column>-->
                                <!--</el-table-column>-->
                                <el-table-column label="" align="left" :show-overflow-tooltip="true">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.cx1Name }}</span>
                                        </template>
                                </el-table-column>
                                <el-table-column label="" align="center">
                                    <el-table-column label="(/m)" align="right" width="120px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.otherPrice?parseFloat(scope.row.otherPrice).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="(/kg)" align="left">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.price?parseFloat(scope.row.price).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table-column>
                                <el-table-column label="(/KG)" align="center">
                                    <el-table-column label="" align="right" width="120px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.pricePart8?parseFloat(scope.row.pricePart8).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.pricePart1?parseFloat(scope.row.pricePart1).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.pricePart2?parseFloat(scope.row.pricePart2).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.pricePart3?parseFloat(scope.row.pricePart3).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.pricePart4?parseFloat(scope.row.pricePart4).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart1).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart2).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart3).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart4).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart5).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart6).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart7).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrPricePart1?parseFloat(scope.row.usrPricePart8).toFixed(3):0.00 }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table-column>
                                <el-table-column label="" align="right" width="80px">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.machineLevelCode }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right" width="80px" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.usrTxt6Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center">
                                    <el-table-column label="" align="center" :show-overflow-tooltip="true">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrTxt1Name }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="right">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrTxt2Name }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="center">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.usrTxt3Name }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table-column>
                                <el-table-column label="" align="center">
                                    <el-table-column label="" align="left" width="120px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.matSeriesCode}}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="left" :show-overflow-tooltip="true">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.matSeriesName }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table-column>
                                <el-table-column label="" align="left" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx2Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx4Name ? parseFloat(scope.row.cx4Name).toFixed(3):0.00 }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx3Name  }}</span>
                                    </template>
                                </el-table-column>

                                <el-table-column label="" align="center">
                                    <el-table-column label="" align="left" width="120px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.settlementMethod}}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" align="left" :show-overflow-tooltip="true">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.settlementMethodName }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table-column>
                                <el-table-column label="" align="center">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.remark }}</span>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div v-if="showTable" style="margin-top: 10px;">
                                <CommonTable
                                    v-if="isResize"
                                    ref="tabTableIII"
                                    :isCenter="true"
                                    :isMulTable=true
                                    :otherVariable="otherVariable"
                                    :formTableData="tabDataIII"
                                    :formPropTable="tabPropIII"
                                    :tableHeight="tableHeightII"
                                >
                                </CommonTable>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                mainHeight: 716,
                isResize: true,
                isShowNotAllowed:false,
                hanleNo:'',
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                tableHeightI: this.$store.state.screenHei*0.62,
                tableHeightII: 150,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    isMulSelect: true,
                    isInlineForm: true,
                    isEdit: false,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    loading: false,
                    $isCustom: true,
                    isSaving:false,

                },
                showTable: false,
                tempWidth: null,
                showButton:false,
                showUnAudit:false,
                tableData: [],
                //
                searchProp: [
                    {
                        name:'',
                        prop:'refDocCode',
                        type:'date',
                        isMainSearch:true,
                        isMain:false,
                        placeholder: ''
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'docCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'docStatusName',type:'outter'},
                    {name:'',prop:'isCheckPrice',type:'outter',isCheck:true},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'contractCode',type:'inner'},
                    {name:'',prop:'alPriceMethodName',type:'inner'},
                    {name:'',prop:'alPrice',type:'inner'},
                    {name:'',prop:'companyName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'settlementMethodName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'refDocCode',
                        name2:'',
                        prop2:'priceStatus',
                        type:'is_check_price',
                        width: '110'
                    },
                    {
                        name1:'/',
                        prop1:'projectCode',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'destination',
                        name2:'',
                        prop2:'loaderMethodName',
                        type:'mixin',
                    }
                ],
                searchForm: {tempStatus: 2,minPriceStatus: 50,maxPriceStatus: 100},
                docStatus:'',
                detailForm: {},
                detailId: null,
                detailIdList: [],
                docCode: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                tabLeft: '100px',
                activeName: 'first',
                //
                tabDataI: [],
                loadingI: false,
                tabPropIII: [
                    {prop:'itemNo', name:'',},
                    {prop:'priceItemText', name:'',type:'normal',width:'100'},
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'otherPrice',name:'(/m)',type:'normal'},
                            {prop:'price',name:'(/kg)',type:'normal'},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                ],
                tabDataIII:[]
            }
        },
        mounted() {
            this.getData();
            // /
            this.listenResizeIII();
            this.changeSizeIII();
            this.tempWidth = this.otherVariable.contentWidth;
        },
        methods: {
            //
            getData(isFirst = true) {
                if(this.searchForm.refDocCode) {
                    this.searchForm = {refDocCode:this.searchForm.refDocCode};
                    this.selectChange(1);
                }else {
                    this.searchForm.refDocCode = null;
                }
                this.tableData = [];
                this.fetch(`/oms/sls/LoaderDoc/getLoaderAndContractInfo?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                        }
                    })
            },
            selectChange(val) {
                if(val === 1) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 101;
                }else if(val === 2) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minPriceStatus = 100;
                    this.searchForm.maxPriceStatus = 100;
                }
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            handleReset(){
                this.searchForm = {tempStatus: 2,minPriceStatus: 50,maxPriceStatus: 100};
                this.handleRefresh();
            },
            //
            handleRefresh() {
                this.getData();
                this.tabDataI = [];
                this.detailForm = {};
                this.showUnAudit = false;
                this.showButton = false;
            },
            //
            changeRefresh() {
                this.getData(false);
                this.otherVariable.isChildEdit = false;
            },
            //
            handleDetail(row) {
                this.isShowNotAllowed = true;
                this.loadingI = true;
                if(row.priceStatus === 0 || row.priceStatus === null || row.priceStatus === 50) {
                    this.showUnAudit = false;
                    this.showButton = true
                }else if(row.priceStatus === 100){
                    this.showUnAudit = true;
                    this.showButton = false
                }else {
                    this.showUnAudit = false;
                    this.showButton = false
                }
                this.detailForm = row;
                this.showTable = false;
                this.tabDataIII = [];
                this.detailId = row.fid;
                this.docStatus = row.docStatus;
                this.docCode = row.docCode;
                this.getDetailTable(row.fid)
            },
            //-
            handleSelectionChange(rows) {
                this.detailIdList = [];
                rows.forEach(item => {
                    this.detailIdList.push(item.fid)
                })
            },
            //
            getDetailTable(fid) {
                this.tabDataI = [];
                this.getAjax(`/oms/sls/SalesLoaderPriceController/getLoaderPrice?loaderFlowId=${fid}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        if(result.status === 200 && result.data) {
                            this.tabDataI = result.data;
                        }else {
                            this.tabDataI = [];
                        }
                    })
            },
            //
            clickDetail(row) {
                this.tabDataIII = [];
                this.showTable = !this.showTable;
                if(this.showTable) {
                    this.isResize = false;
                    this.tableHeightI = this.tableHeightI - 150;
                    this.showTable = true;
                    this.$nextTick(() => {
                        this.isResize = true;
                    })
                }else {
                    this.tableHeightI = this.tableHeightI + 150;
                }
                //
                this.fetch('/oms/sls/SalesOrderPriceController/getMatPriceItem', {matPriceId:row.priceId})
                    .then(result => {
                        if (result.status === 200) {
                            this.tabDataIII = result.data.list;
                        }else {
                            this.tabDataIII = [];
                        }
                    })
                    .then(() => {
                        if(this.showTable) {
                            this.$refs['tabTableIII'].initStatus();
                        }
                    })
            },
            //
            handleAdudit(type) {
                if(this.detailIdList.length>0 && type ==='multi'){
                    this.hanleNo = this.detailIdList.length;

                }else{
                    this.hanleNo='';
                }
                this.$confirm(''+this.hanleNo+'?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let tempObj,api;
                    let isBatch = false;
                    this.otherVariable.isSaving = true;
                    if(type === 'alone') {
                        isBatch = false;
                        tempObj = {loaderFlowId: this.detailId,checkPrice:1};
                        api = '/oms/sls/SalesLoaderPriceController/checkLoaderPriceInfo';
                    }else {
                        isBatch = true;
                        tempObj = {loaderFlowIds: this.detailIdList};
                        api = '/oms/sls/SalesLoaderPriceController/checkLoaderPriceInfoBatch';
                    }
                    if(isBatch && this.detailIdList.length || !isBatch) {
                        let resultStatus;
                        this.fetch(api,tempObj)
                            .then(result => {
                                resultStatus = result.status;
                                if(result.status === 200) {
                                    this.$message({
                                        message: result.data,
                                        type: 'success'
                                    });
                                    if(this.searchForm.tempStatus === 0 && !isBatch) {
                                        this.getData(false);
                                    }else {
                                        this.getData();
                                    }
                                }else {
                                    this.$message({
                                        message: result.data,
                                        type: 'warning'
                                    })
                                }
                            })
                            .then(() => {
                                this.otherVariable.isSaving = false;
                            })
                    }else {
                        this.$message({
                            message: '',
                            type:'warning'
                        })
                    }

                })

            },
            //
            handleUnAudit() {
                let tempObj,api;
                this.otherVariable.isSaving = true;
                tempObj = {loaderFlowId: this.detailId};
                api = '/oms/sls/SalesLoaderPriceController/checkLoaderPriceInfo';
                let resultStatus;
                this.fetch(api,tempObj)
                    .then(result => {
                        resultStatus = result.status;
                        if(result.status === 200) {
                            this.$message({
                                message: result.data,
                                type: 'success'
                            });
                            this.searchForm.tempStatus === 0 ? this.getData(false) : this.getData();
                        }else {
                            this.$message({
                                message: result.data,
                                type: 'warning'
                            })
                        }
                    })
                    .then(() => {
                        this.otherVariable.isSaving = false;
                    })
            }
        }
    }
</script>

